import Vue from 'vue'
import Router from 'vue-router'
import Login from './views/Login.vue'
import Main from "./views/Main.vue";
import MyPage from "./views/Mypage.vue";

Vue.use(Router)

export default new Router({
    mode: 'history',
    base: process.env.BASE_URL,
    routes: [{
            path: '/',
            name: 'login',
            component: Login
        },
        {
            path: '/main',
            name: 'main',
            component: Main
        },
        {
            path: '/mypage',
            name: 'mypage',
            component: MyPage
        },
    ]
})